<template>
    <!-- 登录 -->
    <div id="login">
        <div class="login-form">
            <div class="login-inner">
                <h2>
                    <p>考试成绩查询</p>
                    <p class="f14">GRADE</p>
                </h2>
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm"
                    label-width="100px"
                    class="demo-ruleForm">
                    <el-form-item label="姓名" prop="username">
                        <el-input v-model="ruleForm.username"></el-input>
                    </el-form-item>
                    <el-form-item label="准考证号" prop="cardNum">
                        <el-input v-model="ruleForm.cardNum"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="submitForm('ruleForm')">查询</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            ruleForm:{
                cardNum:'',
                username:''
            },
            rules:{
                cardNum:[
                { required:true,message:'请输入准考证号',trigger:'blur'}
                ],
                username:[
                { required:true,message:'请输入姓名',trigger:'blur'},
                ]
            },
        }
    },
    methods:{
        submitForm(formVal){
            let that=this;
            this.$refs[formVal].validate((valid)=>{
                if(valid){
                    this.$api.querygrade({
                        zkzh: that.ruleForm.cardNum,
                        name: that.ruleForm.username
                    }).then(res=>{
                        if(res.err_code === 100){
                            that.$router.push('/MyGrade');
                        }else{
                            that.$message.error(res.err_msg);
                        }
                    })
                }else{
                    return that.$message.error('请按要求填写');
                }
            })
        }
    }
}
</script>

<style lang="scss" scoped>
    #login{
        padding: 50px 0;
        background: #f5f5f5;

        .login-form{
            margin: auto;
            width: 520px;
            display: flex;
            justify-content: center;
            align-items: center;
            box-sizing: border-box;
            padding: 10px;
            background: #fff;
            
            .login-inner{
              width: 100%;
              height: 100%;
              border: 1.2px solid #4579c7;
              padding: 20px 26px;
              box-sizing: border-box;

              .el-button{
                width: 100%;
              }
              h2{
                font-weight: 500;
                border-bottom: 1px dashed #333;
                text-align: left;
                p.f14{
                  font-weight: 800;
                  font-size: 14px;
                  color: #d5d5d5;
                }
              }
            }
            .el-form{
              margin-top: 10px;
            }           
        }
    }
</style>